
body {
  margin: 0;
  font: 14px/1.4 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  color: #47525d;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  max-width: 560px;
  margin: auto;
  padding: 0 10px;
}
.clearfix {
  clear: both;
}
.markdown-body {
  color: #47525d;
}
.markdown-body a {
  color: #1c90f3;
}
.markdown-body pre {
  border: 1px solid #eaeaea;
  border-radius: 0;
  background-color: transparent;
}
.markdown-body code {
  color: #bd10e0;
  font-family: Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif;
  font-size: 0.9em;
  white-space: pre-wrap;
  background-color: transparent;
}
.markdown-body code:before {
  content: '`';
}
.markdown-body code:after {
  content: '`';
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.loglive-loading {
  font-size: 2rem;
  font-weight: 300;
  color: #b1b0b0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-animation: 1s infinite fade-in-out;
          animation: 1s infinite fade-in-out;
}
.loglive-header {
  border-bottom: 1px solid #f3f3f3;
  padding: 1rem 0;
}
.loglive-header .loglive-title {
  font-weight: 300;
  font-size: 1.125rem;
  margin: 0;
}
.loglive-header .loglive-title a {
  color: #47525d;
  text-decoration: none;
}
.loglive-header .loglive-title a:hover {
  text-decoration: underline;
}
.loglive-header .loglive-title a strong {
  font-weight: 500;
}
.loglive-header .loglive-website a {
  color: rgba(71,82,93,0.35);
  text-decoration: none;
}
.loglive-header .loglive-website a:hover {
  text-decoration: underline;
}
.loglive-body {
  padding: 3rem 0 5rem 0;
  position: relative;
}
.loglive-body .container {
  position: relative;
}
.loglive-body .loglive-sep {
  margin-top: 30px;
}
.loglive-body h1 {
  margin: 0;
  border-bottom: none;
}
.loglive-body ul,
.loglive-body ol {
  padding-left: 20px;
  margin-left: 0;
}
.loglive-body h2 {
  padding-bottom: 0;
  margin-bottom: 0;
  border-bottom: none;
}
.loglive-body .loglive-logtitle {
  margin-top: 50px;
  position: relative;
}
.loglive-body .loglive-logtitle:not(:first-child) {
  border-top: 1px solid #f3f3f3;
  padding-top: 40px;
}
.loglive-body .loglive-logtitle:not(:first-child) .loglive-date {
  margin-top: 66px;
}
.loglive-body .loglive-logtitle .loglive-date {
  font-size: 13px;
  color: rgba(71,82,93,0.4);
}
.loglive-body .loglive-h3 {
  margin-top: 0;
}
.loglive-body .loglive-h3 {
  color: #fff;
  float: left;
  font-weight: 300;
  padding: 3px 12px;
  font-size: 12px;
  border-radius: 33px;
}
@media screen and (min-width: 768px) {
.loglive-logtitle .loglive-date {
    position: absolute;
    left: -130px;
    top: 2px;
}
}
@-webkit-keyframes fade-in-out {
from {
    opacity: 1;
}
50% {
    opacity: 0.5;
}
to {
    opacity: 1;
}
}
@keyframes fade-in-out {
from {
    opacity: 1;
}
50% {
    opacity: 0.5;
}
to {
    opacity: 1;
}
}


